import fs from 'fs';

export default (cb) => {
  try {
    fs.statSync('./manual');
  } catch (err) {
    cb();
    return;
  }

  // The below is required inline because of issues with Gitbook.
  const gitbook = require('gitbook'); // eslint-disable-line global-require
  const book = new gitbook.Book('./manual', {
    config: {
      output: './docs/manual',
    },
  });
  book.parse()
    .then(() => book.generate('website'))
    .then(() => cb())
    .catch((err) => cb(err));
};
